
.u-snippet table {
  width: 100%;
}
.u-snippet table td {
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;
}
.u-snippet table td:hover {
  background-color: whitesmoke;
}
.u-snippet table td .u-snippet-cell-row {
  display: table-row;
  width: 100%;
  min-height: 34px;
}
.u-snippet table td .u-snippet-cell-row .u-snippet-cell-label {
  min-width: 120px;
  display: table-cell;
  text-align: right;
  margin-right: 10px;
  vertical-align: top;
  font-weight: bold;
  margin-bottom: 0;
  padding-right: 10px;
  padding-top: 5px;
  font-size: 14px;
}
.u-snippet table td .u-snippet-cell-row .u-snippet-cell-field {
  display: table-cell;
  width: 2000px;
  vertical-align: bottom;
  /* border-bottom: 1px solid #ccc; */
}
.u-snippet table td .u-snippet-cell-row .u-snippet-cell-field:hover {
  background-color: whitesmoke;
}
.u-snippet table td .u-snippet-cell-row .u-snippet-cell-field .u-generic-input-text {
  font-size: 14px;
}



